.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0.8); 
    backdrop-filter: blur(10px);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
    height: 2.5rem;
  }
  
  .nav-left,
  .nav-right {
    display: flex;
  }
  
  .nav-right {
    gap: 20px;
    font-size: 20px;
  }
  .nav-left{
    font-size: 30px;
  }
  
  ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  li {
    margin: 0;
  }
  
  a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
    padding: 5px 10px; /* Add padding for clickable area */
    border-radius: 5px; /* Slight rounding for hover effect */
    transition: background-color 0.3s, color 0.3s; /* Smooth hover effects */
  }
  
  a:hover {
    color: #007bff; /* Contrast text color */
  }
  